<template>
	<view style="margin-left: 23.5px;margin-right: 26.5px;">
		<view class="cardBox" @click="show">
			<view style="display: flex;flex-direction: row;">
				<view style="width: 280px;box-sizing: border-box;padding: 13px 23px;padding-right: 0;">
					<view style="display: flex; align-items: center;">
						<image :src="cardInfo.image" mode="aspectFill" style="margin-right:10px;"></image>
						<text class="text1">{{cardInfo.userName}}</text>
					</view>
					<view class="content text2">{{cardInfo.content}}</view>
					<view style="display: flex; align-items: center;">
						<text class="text1" style="margin-right:10px;">{{cardInfo.date}}</text>
						<text class="text1">{{cardInfo.time}}</text>
					</view>
				</view>
				<view class="radius"></view>
			</view>
		</view>
		<view style="display: flex;justify-content: center;" @click="show">
			<view v-if="cardInfo.isAnswered" class="answer"></view>
		</view>
		<!-- pop -->
		<view class="pop" v-show="isShow" @click="hide">
			<view style="position: relative;top:100px;" @click.stop="">
				<view class="cardBox">
					<view style="display: flex;flex-direction: row;">
						<view style="width: 280px;box-sizing: border-box;padding: 13px 23px;padding-right: 0;">
							<view style="display: flex; align-items: center;">
								<image :src="cardInfo.image" mode="aspectFill" style="margin-right:10px;"></image>
								<text class="text1">{{cardInfo.userName}}</text>
							</view>
							<view class="content text2">{{cardInfo.content}}</view>
							<view style="display: flex; align-items: center;">
								<text class="text1" style="margin-right:10px;">{{cardInfo.date}}</text>
								<text class="text1">{{cardInfo.time}}</text>
							</view>
						</view>
						<view class="radius" style="border-bottom-right-radius: 0;"></view>
					</view>
				</view>
				<view style="display: flex;justify-content: center;">
					<view v-if="cardInfo.isAnswered" class="answerBox">
						<view style="display: flex; align-items: center;">
							<image :src="cardInfo.image" mode="aspectFill" style="margin-right:10px;"></image>
							<text class="text1" style="color:black;">{{cardInfo.answerName}}</text>
						</view>
						<view class="content text2" style="color:black;">{{cardInfo.answerText}}</view>
					</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: 'lyz_card',
		props: {
			cardInfo: {
				"image": "",
				"userName": "",
				"content": "",
				"date": "",
				"time": "",
				"isAnswered": false,
				"answerText":"",
				"answerName":""
			}
		},
		data() {
			return {
				isShow: false
			}
		},
		methods: {
			show() {
				this.isShow = true;
			},
			hide() {
				this.isShow = false;
			}
		}
	}
</script>

<style scoped>
	.cardBox {
		width: 100%;
		background: #515151;
		border-radius: 10px;
	}

	image {
		width: 24px;
		height: 24px;
		border-radius: 12px;
		background-color: #ffffff;
	}

	.text1 {
		color: #888888;
		font-size: 10px;
	}

	.text2 {
		color: #E2E2E2;
		font-size: 15px;
	}

	.content {
		padding: 10px 0;
		box-sizing: border-box;
		width: 100%;
	}

	.radius {
		width: 23px;
		border-top-right-radius: 10px;
		border-bottom-right-radius: 10px;
		border-right: 3px solid white;
	}

	.answer {
		background: white;
		height: 27px;
		width: 287px;
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
	}
	
	.answerBox{
		background: white;
		width: 287px;
		border-bottom-left-radius: 5px;
		border-bottom-right-radius: 5px;
		background: grey;
		box-sizing: border-box;
		padding: 21px;
	}

	.pop {
		position: fixed;
		top: 0;
		left: 0;
		height: 100%;
		width: 100%;
		background: rgba(0, 0, 0, 0.9);
		box-sizing: border-box;
		padding-left: 23.5px;
		padding-right: 26.5px;

	}
</style>
